import {
    GETALLUSERS_FAILURE,
    GETALLUSERS_REQUEST,
    GETALLUSERS_SUCCESS,
} from "../../constants/auth/getUsersConstants";
import {
    GetAllUserAction,
    GetAllUserState,
} from "../../interfaces/auth/userInterfaces";

export function users(
    state: GetAllUserState = {},
    action: GetAllUserAction = {}
): GetAllUserState {
    switch (action.type) {
        case GETALLUSERS_REQUEST:
            return {
                loading: true,
            };
        case GETALLUSERS_SUCCESS:
            return {
                items: action.payload.users,
            };
        case GETALLUSERS_FAILURE:
            return {
                error: action.payload.error,
            };
        default:
            return state;
    }
}
